<template>
  <div class="pretreat_case">
    <listSearch v-model="searchFormData" @searchConfirm="goSearch" :searchArr="searchArr"></listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="btnOperate(3)">批量受理</Button>
        <Button type="primary" @click="btnOperate(4)" style="margin-left: 8px">批量驳回</Button>
      </div>
      <div class="table_box mgt20">
        <Table
          @on-select="selectOne"
          @on-select-cancel="selectCancel"
          @on-select-all="selectAll"
          @on-select-all-cancel="selectAllCancel"
          :columns="columns"
          :data="tableData"
          show-sizer
          show-elevator
          show-total
        >
          <template slot-scope="{ row }" slot="caseId">
            <a href="javascript:">{{row.id}}</a>
          </template>
          <template slot-scope="{ row }" slot="proxyName">
            <span>{{row['proxyName']}}</span>
          </template>
           <template slot-scope="{ row }" slot="state">
            <span>{{caseStatus[row.state]}}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div>
              <a href="javascript:;" style="margin-right:20px" @click="btnOperate(1,row)" title="受理"><img width="26" height="26" src="../../../assets/images/resolve.png" alt />
              </a>
              <a href="javascript:;" @click="btnOperate(2,row)" title="驳回">
                <img width="26" height="26" src="../../../assets/images/reject.png" alt />
              </a>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page
          :current="currentPage"
          :total="total"
          :page-size="pageSize"
          :page-size-opts="pageOpts"
          @on-change="changeCurrentPage"
          @on-page-size-change="changeSize"
          show-sizer
          show-elevator
          show-total
        />
      </div>
    </div>
    <!-- 弹窗部分 -->
    <popupBox
      :show="popupShow"
      :titleText="popupTypeObj[popupType]['title']"
      :width="'740'"
      @popupConfirm="popupConfirm"
      @popupCancel="popupCancel"
    >
      <!-- 驳回 -->
      <div class="reject_content" v-if="popupType == 2 || popupType == 4">
        <div class="title">请输入驳回原因：</div>
        <textarea v-model="rejectReason" name id cols="30" rows="10"></textarea>
      </div>
      <!-- 受理 -->
      <div class="accept_content" v-if="popupType == 1 || popupType == 3">
        <div class="item" v-for="item in acceptList" :key="item.id">
          <div class="item_content">
            <div class="label">案件编号：</div>
            <div class="val">
              <span>{{item.id}}</span>
            </div>
          </div>
          <div class="item_content">
            <div class="label">请输入纠纷金额(元)：</div>
            <div class="val">
              <input type="text" v-model="item.money" />
            </div>
          </div>
          <div class="item_content">
            <div class="label">仲裁金额(元)：</div>
            <div class="val">
              <input type="text" v-model="item.cost" />
            </div>
          </div>
        </div>
      </div>
    </popupBox>
  </div>
</template>

<script>
import "./pretreat_case.less";
import listSearch from "@/components/list-search/search.vue";
import { listMixin } from "../listMixin";
// import zzlist from '@/components/zzlist/zzlist'
import { mapActions } from "vuex";
export default {
  name: "pretreat_case",
  components: { listSearch },
  mixins: [listMixin],
  data() {
    return {
      popupShow: false,
      popupType: 1,
      popupTypeObj: {
        1: { type: 1, title: "案件受理" },
        2: { type: 2, title: "案件驳回（单案）" },
        3: { type: 3, title: "案件受理（批量）" },
        4: { type: 4, title: "案件驳回（批量）" }
      },
      acceptList: [], // 受理
      rejectList: [1, 2],
      rejectReason: null,
      searchFormData: {
        number: "",
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: 2, //注册名称的用户id
        caseTypeId: 2, //案件类型id
        timePeriod: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案件编号",
          placeholder: "请输入个人姓名"
        },
        {
          type: "input",
          value: "applicantName",
          label: "申请人",
          placeholder: "请输入企业名称"
        },
        {
          type: "input",
          value: "respondentName",
          label: "被申请人",
          placeholder: "请输入企业名称"
        },
        {
          type: "select",
          value: "userId",
          label: "注册名称",
          arr: [
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "select",
          value: "caseTypeId",
          label: "案件类型",
          arr: [
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "datePicker",
          value: "createTimeBegin",
          label: "提交时间",
          placeholder: "选择时间"
        }
      ],
      columns: [
        {
          type: "selection",
          width: 100,
          align: "center"
        },
        {
          title: "案件编号",
          key: "id",
          slot: "caseId",
          width: 200,
          align: "center"
        },
        {
          title: "申请人",
          key: "applicantName",
          align: "center"
        },
        {
          title: "代理人",
          key: "proxyName",
          slot:"proxyName",
          align: "center"
        },
        {
          title: "被申请人",
          key: "respondentName",
          align: "center"
        },
        {
          title: "案件类型",
          key: "caseTypeName",
          align: "center"
        },
        {
          title: "提交时间",
          key: "createTime",
          align: "center",
          width: 200
        },
        {
          title: "固化状态",
          key: "",
          align: "center"
        },
        {
          title: "案件状态",
          key: "state",
          slot:"state",
          align: "center"
        },
        {
          title: "仲裁费",
          key: "cost",
          align: "center"
        },
        {
          title: "操作",
          slot: "action",
          align: "center"
        }
      ],
      caseStatus:{
        1:'待受理',
        2:"已驳回",
        3:"待缴费",
        4:"待立案",
        5:"立案审核中",
        6:"立案成功",
        7:'待组庭',
        8:'待指定仲裁员',
        9:'待确认或修改仲裁员',
        10:"待选择审理方式",
        11:"待开庭",
        12:'待结案'
      }
    };
  },
  mounted() {},
  methods: {
    ...mapActions({
      getRequsetList: "createCase/getRequsetList",
      acceptance: "createCase/acceptance", //单个受理
      acceptanceAll: "createCase/acceptanceAll", // 批量受理
      rejectCase: "createCase/rejectCase", // 驳回
      rejectCaseAll: "createCase/rejectCaseAll" // 批量驳回
    }),
    // 数组里包含对象的格式去重
    duplicate(arr, type) {
      if (arr.length == 0) {
        return arr;
      } else {
        if (type) {
          var obj = {};
          var newArr = arr.reduce((cur, next) => {
            obj[next.name] ? "" : (obj[next.name] = true && cur.push(next));
            return cur;
          }, []);
          return newArr;
        } else {
          return Array.from(new Set(arr));
        }
      }
    },
    async getList() {
      let params = {
        menuIdentifer: "ACCEPT_CASE_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter = {
        //筛选条件
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: "", //注册名称的用户id
        caseTypeId: "", //案件类型id
        createTimeBegin: "", //提交的开始时间
        createTimeEnd: "" //提交的结束时间
      };

      params = { ...params, filter: JSON.stringify(filter) };
      // console.log("参数", params);
      const res = await this.getRequsetList(params);

      if (res.data.flag) {
        // console.log(res.data.data);
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
      }
    },
    // 搜索
    goSearch() {
      console.log(this.searchFormData);
    },
    // 选择发生改变
    // 按钮此作
    btnOperate(type, row) {
      // 1:受理 2:驳回 3:批量受理 4:批量驳回
      console.log(type, row);
      let length = this.selectedData.length;
      if (type == 3 && length === 0) {
        this.$Message.warning("请至少选择一条案件信息");
        return;
      }
      // "money": 0,//纠纷金额
      // "cost": 100,//仲裁费
      // id
      if (type == 1 || type == 2) {
        // 单个受理
        this.acceptList.push(row);
      }
      if (type == 3 || type == 4) {
        // 批量受理
      
        this.tableData.map((item, index) => {
          if (this.selectedData.includes(item.id)) {
            this.acceptList.push(item);
          }
        });
      }

      console.log(this.acceptList,':acceptList');
      this.popupType = type;
      this.popupShow = true;
    },
    // 弹窗确认
    popupConfirm() {
      // console.log(1);
      // 1:受理 2:驳回 3:批量受理 4:批量驳回
      console.log("类型", this.popupType);
      if (this.popupType == 1) {
        // 单个
        this.submitOne();
      } else if (this.popupType == 3) {
        // 批量受理
        this.submitAll();
      } else if (this.popupType == 2) {
        // 单个驳回
        this.submitBackOne();
      } else if (this.popupType == 4) {
        // 批量驳回
        this.submitBackAll();
      }
    },
    async submitAll() {
      let arr = [];
      this.acceptList.map(item => {
        let obj = {};
        obj["caseId"] = item.id;
        obj["actionIdentifer"] = "ACCEPT_PASS";
        obj["cost"] = item.cost;
        arr.push(obj);
      });
      // let params= {
      //       caseId:"2020000001",//案件id
      //       actionIdentifer:"ACCEPT_PASS", //操作标识
      //       cost:100
      //   }
      const result = await this.acceptanceAll({ arr: JSON.stringify(arr) });
      console.log(result, ":批量审理");
      if (result.data.flag) {
        this.$Message.success('操作成功！');
        
        //  重置
        this.popupCancel();
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    async submitOne() {
      let params = {};
      // let params= {
      //     caseId:"2020000001",//案件id
      //     actionIdentifer:"ACCEPT_PASS", //操作标识
      //     cost:100
      // }
      if (this.acceptList.length ==1) {
        params["caseId"] = this.acceptList[0].id;
        params["cost"] = this.acceptList[0].cost;
      }

      const result = await this.acceptance({
        actionIdentifer: "ACCEPT_PASS",
        ...params
      });

      if (result.data.flag) {
        this.$Message.success('操作成功！');

        //  重置
        this.popupCancel();
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    async submitBackOne() {
      let params = {};
      if (this.acceptList.length > 0) {
        params["caseId"] = this.acceptList[0].id;
        params["reason"] = this.rejectReason;
        params["actionIdentifer"] = "ACCEPT_REJECT";
      }
      const result = await this.rejectCase(params);
      if (result.data.flag) {
        this.$Message.success({
          content: "操作成功！",
          duration: 3000
        });

        //  重置
        this.popupCancel();
        this.getList()
      } else {
        this.$Message.error({
          content: result.data.message,
          duration: 3000
        });
      }
    },
    async submitBackAll(){
      let arr = [];
      this.acceptList.map(item => {
        let obj = {};
          obj["caseId"] = item.id
          obj["reason"] = this.rejectReason;
          obj["actionIdentifer"] = "ACCEPT_REJECT";
        arr.push(obj);
      });
      const result = await this.rejectCaseAll({ arr: JSON.stringify(arr) });
      console.log(result, ":批量驳回");
      if (result.data.flag) {
        this.$Message.success('操作成功！')
        //  重置
        this.popupCancel();
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }

    },
    // 弹窗取消
    popupCancel() {
      console.log(1);
      this.acceptList = [];
      this.popupType = 1;
      this.popupShow = false;
      this.rejectReason=''
    }
  }
};
</script>

<style lang="less" scoped>
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
@import url("../../../style/iview_table.less");
</style>